import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import xinwenzixunList from '@/views/pages/xinwenzixun/list'
import xinwenzixunDetail from '@/views/pages/xinwenzixun/formModel'
import xinwenzixunAdd from '@/views/pages/xinwenzixun/formAdd'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import mangbanshenqingList from '@/views/pages/mangbanshenqing/list'
import mangbanshenqingDetail from '@/views/pages/mangbanshenqing/formModel'
import mangbanshenqingAdd from '@/views/pages/mangbanshenqing/formAdd'
import mangbananzhuangList from '@/views/pages/mangbananzhuang/list'
import mangbananzhuangDetail from '@/views/pages/mangbananzhuang/formModel'
import mangbananzhuangAdd from '@/views/pages/mangbananzhuang/formAdd'
import mangbanxunjianList from '@/views/pages/mangbanxunjian/list'
import mangbanxunjianDetail from '@/views/pages/mangbanxunjian/formModel'
import mangbanxunjianAdd from '@/views/pages/mangbanxunjian/formAdd'
import mangbanchaichuList from '@/views/pages/mangbanchaichu/list'
import mangbanchaichuDetail from '@/views/pages/mangbanchaichu/formModel'
import mangbanchaichuAdd from '@/views/pages/mangbanchaichu/formAdd'
import aboutusList from '@/views/pages/aboutus/list'
import aboutusDetail from '@/views/pages/aboutus/formModel'
import aboutusAdd from '@/views/pages/aboutus/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'xinwenzixunList',
			component: xinwenzixunList
		}, {
			path: 'xinwenzixunDetail',
			component: xinwenzixunDetail
		}, {
			path: 'xinwenzixunAdd',
			component: xinwenzixunAdd
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'mangbanshenqingList',
			component: mangbanshenqingList
		}, {
			path: 'mangbanshenqingDetail',
			component: mangbanshenqingDetail
		}, {
			path: 'mangbanshenqingAdd',
			component: mangbanshenqingAdd
		}
		, {
			path: 'mangbananzhuangList',
			component: mangbananzhuangList
		}, {
			path: 'mangbananzhuangDetail',
			component: mangbananzhuangDetail
		}, {
			path: 'mangbananzhuangAdd',
			component: mangbananzhuangAdd
		}
		, {
			path: 'mangbanxunjianList',
			component: mangbanxunjianList
		}, {
			path: 'mangbanxunjianDetail',
			component: mangbanxunjianDetail
		}, {
			path: 'mangbanxunjianAdd',
			component: mangbanxunjianAdd
		}
		, {
			path: 'mangbanchaichuList',
			component: mangbanchaichuList
		}, {
			path: 'mangbanchaichuDetail',
			component: mangbanchaichuDetail
		}, {
			path: 'mangbanchaichuAdd',
			component: mangbanchaichuAdd
		}
		, {
			path: 'aboutusList',
			component: aboutusList
		}, {
			path: 'aboutusDetail',
			component: aboutusDetail
		}, {
			path: 'aboutusAdd',
			component: aboutusAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
